import { makeExecutionTree } from "../build-frame-tree";
import { msgpackLoad } from "../extension/msgpack";

import WebApp from "./WebApp.svelte";

async function loadVizForTrace(traceId: string) {
  const trace = await fetch(`api/traces/${traceId}/`);

  let parsed_trace;
  if (trace.headers.get("content-type") === "application/msgpack") {
    parsed_trace = msgpackLoad(new Uint8Array(await trace.arrayBuffer()));
  } else {
    parsed_trace = await trace.json();
  }

  const tree = makeExecutionTree(parsed_trace.frames_of_interest);

  window.dispatchEvent(
    new CustomEvent("web-load-viz", {
      detail: { tree, traceId },
    })
  );
}

async function main() {
  new WebApp({
    target: document.getElementById("app") as HTMLElement,
  });

  const latest_traces = await (await fetch("api/latest-traces/")).json();

  const tracesElement = document.getElementById("traces");
  tracesElement!.innerHTML = latest_traces.traces
    .map((trace_id: string) => {
      return (
        '<div class="trace my-1 p-2 border-2 rounded-md cursor-pointer">' +
        trace_id +
        "</div>"
      );
    })
    .join("");

  const traceElements = document.getElementsByClassName("trace");
  // @ts-ignore
  for (const traceElement of traceElements) {
    // @ts-ignore
    traceElement.addEventListener("click", (event) => {
      // @ts-ignore
      const trace_id = event.target.innerHTML;
      loadVizForTrace(trace_id);
    });
  }

  const latest_trace_id = latest_traces.traces[0];
  loadVizForTrace(latest_trace_id);
  window
    .matchMedia("(prefers-color-scheme: dark)")
    .addEventListener("change", () => {
      window.dispatchEvent(
        new CustomEvent("message", { detail: { command: "themeChange" } })
      );
    });
}

document.addEventListener("DOMContentLoaded", main);
