import { addExtension, Packr } from "msgpackr";

let packr = new Packr({ useRecords: false });

// DateTime extension
addExtension({
  type: 0,
  unpack(buffer) {
    return buffer.toString("utf-8");
  },
});

addExtension({
  type: 1,
  unpack(buffer) {
    return buffer.toString("utf-8");
  },
});

// Date extension
addExtension({
  type: 2,
  unpack(buffer) {
    return buffer.toString("utf-8");
  },
});

// Time extension
addExtension({
  type: 3,
  unpack(buffer) {
    return buffer.toString("utf-8");
  },
});

// https://coolaj86.com/articles/convert-hex-to-decimal-with-js-bigints/
function hexToBigInt(hex: String) {
  if (hex.length % 2) {
    hex = "0" + hex;
  }

  var highbyte = parseInt(hex.slice(0, 2), 16);
  var bn = BigInt("0x" + hex);

  if (0x80 & highbyte) {
    // bn = ~bn; WRONG in JS (would work in other languages)

    // manually perform two's compliment (flip bits, add one)
    // (because JS binary operators are incorrect for negatives)
    bn =
      BigInt(
        "0b" +
          bn
            .toString(2)
            .split("")
            .map(function (i: any) {
              return "0" === i ? 1 : 0;
            })
            .join("")
      ) + BigInt(1);
    // add the sign character to output string (bytes are unaffected)
    bn = -bn;
  }

  return bn;
}

// Integer extension
addExtension({
  type: 4,
  unpack(buffer) {
    let hex = buffer.toString("hex");
    return hexToBigInt(hex);
  },
});

// Decimal extension
addExtension({
  type: 5,
  unpack(buffer) {
    return Number(buffer.toString("utf-8"));
  },
});

addExtension({
  type: 127,
  unpack(buffer) {
    let broken_class = buffer.toString("utf-8");
    return `SerializationError: Unserializable \`${broken_class}\` instance`;
  },
});

// Turn this binary value mostly into the javascript object that JSON.parse
// would give us. For now, set all locals to empty {}  so that we just deal with
// the "easier" bits for now.
export function msgpackLoad(buffer: Uint8Array) {
  return packr.unpack(buffer);
}

// [null, null] -> None, None
// True / False (including in nested places)
// datetimes as their repr (but we want the real datetime for test generation)
